/* BODY - styling for border animation */
.invitationBorder {
  overflow: hidden;
  position: relative;
}

.invitationBorder::before,
.invitationBorder::after {
  content: '';
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  border: 4px solid transparent;
  width: 0;
  height: 0;
}

.invitationBorder::before {
  top: 0;
  left: 0;
  -webkit-animation: border 2.3s 1 ease-in-out;
          animation: border 2.3s 1 ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  border-top-color: #ffc7a2;
  border-right-color: #ffc7a2;
}

.invitationBorder::after {
  bottom: 0;
  right: 0;
  -webkit-animation: border 2.3s 1.3s 1 ease-in-out, borderColor 3s 1.3s 1 ease-in-out;
          animation: border 2.3s 1.3s 1 ease-in-out, borderColor 3s 1.3s 1 ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes border {
  0% {
    width: 0;
    height: 0;
  }

  25% {
    width: 100%;
    height: 0;
  }

  50% {
    width: 100%;
    height: 100%;
  }

  100% {
    width: 100%;
    height: 100%;
  }
}

@keyframes border {
  0% {
    width: 0;
    height: 0;
  }

  25% {
    width: 100%;
    height: 0;
  }

  50% {
    width: 100%;
    height: 100%;
  }

  100% {
    width: 100%;
    height: 100%;
  }
}

@-webkit-keyframes borderColor {
  0% {
    border-bottom-color: #ffc7a2;
    border-left-color: #ffc7a2;
  }

  100% {
    border-bottom-color: #ffc7a2;
    border-left-color: #ffc7a2;
  }
}

@keyframes borderColor {
  0% {
    border-bottom-color: #ffc7a2;
    border-left-color: #ffc7a2;
  }

  100% {
    border-bottom-color: #ffc7a2;
    border-left-color: #ffc7a2;
  }
}

@-webkit-keyframes borderColorOriginal {
  0% {
    border-bottom-color: #60daaa;
    border-left-color: #60daaa;
  }

  50% {
    border-bottom-color: #60daaa;
    border-left-color: red;
  }

  51% {
    border-bottom-color: transparent;
    border-left-color: transparent;
  }

  100% {
    border-bottom-color: transparent;
    border-left-color: transparent;
  }
}

@keyframes borderColorOriginal {
  0% {
    border-bottom-color: #60daaa;
    border-left-color: #60daaa;
  }

  50% {
    border-bottom-color: #60daaa;
    border-left-color: red;
  }

  51% {
    border-bottom-color: transparent;
    border-left-color: transparent;
  }

  100% {
    border-bottom-color: transparent;
    border-left-color: transparent;
  }
}


